<template>
    <div class="recommend" ref="recommend">
        <scroll ref="scroll" :data="discList" class="recommend-content">
            <div>
                <div v-if="recommends.length" class="slider-wrapper">
                    <Slider>
                        <div v-for="item in recommends">
                            <a :href="item.linkUrl">
                                <img @load="loadImage" :src="item.picUrl" class="needsclick">
                            </a>
                        </div>
                    </Slider>
                </div>
                <div class="recommend-list">
                    <h1 class="list-title">热门歌单推荐</h1>
                    <ul>
                        <li @click="selectItem(e)" v-for="(e,i) in discList" class="item">
                            <div class="icon">
                                <img v-lazy="e.imgurl">
                            </div>
                            <div class="text">
                                <h2 class="name" v-html="e.creator.name">{{}}</h2>
                                <p class="desc" v-html="e.dissname"></p>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="loading-container" v-show="!discList.length">
                <Loading></Loading>
            </div>
        </scroll>
        <router-view></router-view>
    </div>
</template>

<script>
import {getRecommend,getDiscList} from 'api/recommend'
import {ERR_OK} from 'api/config'
import Slider from 'base/slider/slider'
import Scroll from 'base/scroll/scroll'
import Loading from 'base/loading/loading'
import {playListMixin} from 'common/js/mixin'
import {mapMutations} from 'vuex'

export default{
    mixins:[playListMixin],
    data(){
        return{
            recommends:[],
            discList:[]
        }
    },
    methods:{
        selectItem(e){
            this.$router.push(`/recommend/${e.dissid}`)
            this.setDisc(e)
        },
        handlePlayList(){
            const bottom = this.playList.length>0 ? '60px' : '0'
            this.$refs.recommend.style.bottom = bottom
            this.$refs.scroll.refresh()
        },
        _getRecommend(){
            getRecommend().then((res)=>{
                if(res.code === ERR_OK){
                    this.recommends = res.data.slider
                }
            })
        },
        _getDiscList(){
            getDiscList().then((res)=>{
                if (res.code === ERR_OK) {
                    this.discList = res.data.list
                };
            })
        },
        loadImage(){
            if (!this.checkLoaded) {
                this.$refs.scroll.refresh();
                this.checkLoaded = true
            };
        },
        ...mapMutations({
            setDisc:'SET_DISC'
        })

    },
    components:{
        Slider,
        Scroll,
        Loading
    },
    created(){
        this._getRecommend()
        this._getDiscList()
    }
}
</script>

<style lang="stylus">
@import "~common/stylus/variable"
.recommend
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .recommend-content
      height: 100%
      overflow: hidden
      .slider-wrapper
        position: relative
        width: 100%
        overflow: hidden
      .recommend-list
        .list-title
          height: 65px
          line-height: 65px
          text-align: center
          font-size: $font-size-medium
          color: $color-theme
        .item
          display: flex
          box-sizing: border-box
          align-items: center
          padding: 0 20px 20px 20px
          .icon
            flex: 0 0 60px
            width: 60px
            padding-right: 20px
            img
                width:60px
                height:60px
          .text
            display: flex
            flex-direction: column
            justify-content: center
            flex: 1
            line-height: 20px
            overflow: hidden
            font-size: $font-size-medium
            .name
              margin-bottom: 10px
              color: $color-text
            .desc
              color: $color-text-d
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>


